.loginBox {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 400px;
  padding: 40px;
  transform: translate(-50%, -50%);
  background: rgba(0, 0, 0, 0.5);
  box-sizing: border-box;
  box-shadow: 0 15px 25px rgba(0, 0, 0, 0.6);
  border-radius: 10px;
}

.loginBox h2 {
  margin: 0 0 30px;
  padding: 0;
  color: #fff;
  text-align: center;
  font-size: 30px;
  text-decoration: underline;
  text-underline-offset: 8px;
}

.loginBox .userBox {
  position: relative;
  margin-bottom: 30px;
  display: flex;
  flex-direction: column; /* Agregado */
}

.loginBox .userBox input {
  width: 100%;
  padding: 10px 0;
  font-size: 16px;
  color: #fff;
  margin-bottom: 5px;
  border: none;
  border-bottom: 1px solid #fff;
  outline: none;
  background: transparent;
  transition: border-bottom-color 0.3s ease;
}

.loginBox .userBox label {
  font-size: 20px;
  font-weight: bold;
  color: #ffffffb1;
  transition: transform 0.3s ease, font-size 0.3s ease, color 0.3s ease;
}

.loginBox .userBox input:focus,
.loginBox .userBox input:valid {
  border-bottom-color: #32f128;
}

.loginBox .userBox input:focus + label,
.loginBox .userBox input:valid + label {
  transform: translateY(-25px);
  font-size: 12px;
  opacity: 1;
  color: #03e9f4;
}

.loginBox .userBox p {
  margin: 5px 0 0;
  font-size: 18px;
  font-weight: bold;
  color: #fb0000;
}

.loginBox form button {
  position: relative;
  display: block;
  padding: 10px 20px;
  color: #32f128;
  font-size: 16px;
  font-weight: bold;
  text-decoration: none;
  text-transform: uppercase;
  overflow: hidden;
  transition: 0.5s;
  margin: 40px auto 0;
  margin-top: 40px;
  letter-spacing: 4px;
}

.loginBox button:hover {
  background: #32f128;
  color: #fff;
  border-radius: 5px;
  box-shadow: 0 0 5px #32f128, 0 0 25px #32f128, 0 0 50px #32f128,
    0 0 100px #32f128;
}

.loginBox button span {
  position: absolute;
  display: block;
}

.loginBox button span:nth-child(1) {
  top: 0;
  left: -100%;
  width: 100%;
  height: 5px;
  background: linear-gradient(90deg, transparent, #32f128);
  animation: btn-anim1 1s linear infinite;
}

@keyframes btn-anim1 {
  0% {
    left: -100%;
  }
  50%,
  100% {
    left: 100%;
  }
}

.loginBox button span:nth-child(2) {
  top: -100%;
  right: 0;
  width: 5px;
  height: 100%;
  background: linear-gradient(180deg, transparent, #32f128);
  animation: btn-anim2 1s linear infinite;
  animation-delay: 0.25s;
}

@keyframes btn-anim2 {
  0% {
    top: -100%;
  }
  50%,
  100% {
    top: 100%;
  }
}

.loginBox button span:nth-child(3) {
  bottom: 0;
  right: -100%;
  width: 100%;
  height: 5px;
  background: linear-gradient(270deg, transparent, #32f128);
  animation: btn-anim3 1s linear infinite;
  animation-delay: 0.5s;
}

@keyframes btn-anim3 {
  0% {
    right: -100%;
  }
  50%,
  100% {
    right: 100%;
  }
}

.loginBox button span:nth-child(4) {
  bottom: -100%;
  left: 0;
  width: 5px;
  height: 100%;
  background: linear-gradient(360deg, transparent, #32f128);
  animation: btn-anim4 1s linear infinite;
  animation-delay: 0.75s;
}

@keyframes btn-anim4 {
  0% {
    bottom: -100%;
  }
  50%,
  100% {
    bottom: 100%;
  }
}

.example {
  width: 100px;
  height: 100px;
}

.example p {
  line-height: 0;
  color: rgb(157, 169, 151);
}
